.checkout-page {
  padding: 23px 0 153px;
  &__title {
    margin-bottom: 18px; } }

.checkout-form-card {
  @extend %card;
  padding: 38px 30px 50px; }

.checkout-form {
  &__title {
      margin-bottom: 24px; }
  &__row {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -23px; }
  &__col {
    position: relative;
    @include flex-size(25%);
    padding: 0 23px;
    margin-bottom: 20px;
    @include md {
      @include flex-size(50%); }
    @include sm {
      @include flex-size(100%); }
    &_wide {
      @include flex-size(50%); } } }
