.search {
  position: relative;
  &__input {
    position: absolute;
    right: 0;
    top: 0;
    @include fadeout($tr);
    max-width: 200px;
    background-color: transparent;
    border-bottom: 1px solid $white; }
  &__button {
    display: inline-block;
    background-color: transparent;
    font-size: 0;
    .icon {
      font-size: 12px;
      color: $white; } } }

.signup-form,
.subscr-form {
  position: relative;
  width: 100%; }

.input-group {
  position: relative;
  width: 100%;
  &__input {
    width: 100%;
    color: $text;
    border-bottom: 1px solid #e0e0e0;
    background-color: transparent;
    padding: 10px 10px 10px 0;
    font-family: $montserrat;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.2;
    transition: border-bottom-color $tr;
    @include text-overflow;
    .signup-form & {
      color: $white;
      padding-right: 60px; }
    .subscr-form & {
      padding-right: 60px; }
    &:placeholder {
      color: #a8a6a6; }
    &:focus {
      border-bottom-color: $blackBG;
      .signup-form & {
        border-bottom-color: $white; }
      + .input-group__btn {
        color: $orange; } }
    &_textarea {
      white-space: pre-wrap;
      padding-right: 60px; }
    &.error {
      border-bottom: 1px solid $extraorange !important; } }
  &__btn {
    position: absolute;
    right: 0;
    bottom: 10px;
    display: inline-block;
    background-color: transparent;
    color: $blue;
    font-family: $montserrat;
    font-weight: 500;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.2;
    transition: color $tr;
    @include hover {
      color: $orange; } } }

textarea {
  border: none;
  border-bottom: 1px solid #e0e0e0;
  height: 34px;
  resize: none;
  box-sizing: border-box;
  display: block;
  overflow: hidden; }

.adress-form {
  &__label {
    display: block;
    margin-bottom: 13px;
    @extend %title;
    cursor: pointer; }
  &__checkbox {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    height: 0;
    &:checked {
      ~ .adress-form__hidden {
        display: block; } } }
  &__field {
    width: 100%;
    &:not(:last-child) {
      margin-bottom: 8px; } }
  &__hidden {
    display: none; } }

.bg-input {
  width: 100%;
  padding: 4px 4px 4px 8px;
  border-radius: $radius;
  background-color: #f7f7f7;
  @extend %title;
  color: $text;
  border: 1px solid #f7f7f7;
  &:placeholder {
    color: $mainText; }
  &:focus {
    border: 1px solid $blue; } }

.help-block.form-error {
  position: absolute;
  top: 100%;
  left: 0;
  padding-top: 3px;
  color: $extraorange;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.2;
  .checkout-form__col & {
    left: 23px; } }

.contact-form {
  max-width: 697px;
  width: 100%;
  &__title {
    margin-bottom: 46px;
    color: $text;
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.25;
    text-align: center;
    @include md {
      margin-bottom: 36px; }
    @include sm {
      margin-bottom: 24px; } }
  &__row {
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    &:not(:last-child) {
      margin-bottom: 27px; } }
  &__col {
    position: relative;
    @include flex-size(38.5%);
    @include sm {
      @include flex-size(100%);
      &:not(:last-child) {
        margin-bottom: 27px; } } } }
